import { styled } from "@mui/material/styles";
import { Box, Table, Button, TextField } from "@mui/material";
import { useFormik } from "formik";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import axios from "axios";
import { useEffect, useState } from "react";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.secondary.main,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  "&:nth-of-type(odd)": {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  "&:last-child td, &:last-child th": {
    border: 0,
  },
}));

export default function ReusableTables() {
  const [jobsReaArr, setJobsResArr] = useState([]);

  const formik = useFormik({
    initialValues: {
      job_name: "",
      server_name: "",
      states: "",
    },
    onSubmit: (values) => {
      axios
        .post("http://127.0.0.1:8090/api/collections/jobs/records", values)
        .then((res) => {
          getJob();
          alert("updated");
        })
        .catch((err) => {
          alert("some thing going wrong");
        });
    },
  });

  const getJob = () => {
    axios
      .get("http://127.0.0.1:8090/api/collections/jobs/records")
      .then((res) => {
        setJobsResArr(res.data.items);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  useEffect(() => {
    getJob();
  }, []);

  const deleteHandler = (id) => {
    console.log(id);
    axios
      .delete("http://127.0.0.1:8090/api/collections/jobs/records/" +id)
      .then(() => {
        getJob();
        alert("deleted");
      })
      .catch((err) => {
        alert("some thing going wrong");
      });
  };
  return (
    <Box>
      <Box py={3}>
        <FormControl>
          <TextField
            fullWidth
            name="job_name"
            onChange={formik.handleChange}
            value={formik.values.job_name}
          />
          <TextField
            fullWidth
            my={2}
            onChange={formik.handleChange}
            value={formik.values.server_name}
            name="server_name"
          />
          <FormLabel id="demo-row-radio-buttons-group-label">States</FormLabel>

          <RadioGroup
            row
            name="states"
            onChange={formik.handleChange}
            value={formik.values.states}
          >
            <FormControlLabel
              value="running"
              control={<Radio />}
              label="running"
            />
            <FormControlLabel
              value="writing"
              control={<Radio />}
              label="writing"
            />
            <FormControlLabel
              value="finished"
              control={<Radio />}
              label="finished"
            />
          </RadioGroup>
          <Button variant="contained" onClick={formik.handleSubmit}>
            create new jop
          </Button>
        </FormControl>
      </Box>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 700 }} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell>job name</StyledTableCell>
              <StyledTableCell> server name </StyledTableCell>
              <StyledTableCell>states</StyledTableCell>
              <StyledTableCell>last update </StyledTableCell>
              <StyledTableCell>delete </StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {jobsReaArr?.map((row) => (
              <StyledTableRow key={row.id}>
                <StyledTableCell component="th">
                  {row?.job_name}
                </StyledTableCell>
                <StyledTableCell>{row?.server_name}</StyledTableCell>
                <StyledTableCell>{row?.states}</StyledTableCell>
                <StyledTableCell>{row?.updated}</StyledTableCell>
                <StyledTableCell>
                  <Button onClick={() => deleteHandler(row.id)}>delete</Button>
                </StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
}
