import { useState } from "react";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import TopNav from "../src/components/Navbar/TopNav";
import SideNav from "../src/components/Navbar/SideNav";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import theme from "./utils/theme"; 
import ReusableTables from "./components/ReusableTables";
const drawerWidth = 240;

function App() {

  const [mobileOpen, setMobileOpen] = useState(false);

  const openHandel = () => {
    setMobileOpen(!mobileOpen);
  };

  const ltrTheme = createTheme({ direction: "ltr", ...theme });
const rtlTheme = createTheme({ direction: "rtl", ...theme });
  return (
    <ThemeProvider theme={false ? rtlTheme : ltrTheme}>
    <Box sx={{ display: "flex", overflow: "hidden" }}>
      <TopNav openHandel={openHandel} drawerWidth={drawerWidth} />

      <SideNav
        openHandel={openHandel}
        mobileOpen={mobileOpen}
        drawerWidth={drawerWidth}
      />

      <Box component="main" sx={BoxStyle}>
        <Toolbar />
        <ReusableTables />
      </Box>
    </Box>
    </ThemeProvider>
  );
}

export default App;

const BoxStyle = {
  flexGrow: 1,
  p: 3,
  width: { xs: "100vw", md: `calc(100% - ${drawerWidth}px)` },
};
